
.dialogsItems {
   display: grid;
   grid-template-columns: 3fr 7fr;
   
}

.dialogsItem {
   border-right: solid 1px gray;
   padding: 2em;
}
.body {
   display: flex;
   align-items: center;
}
.link {
   font-weight: 600;
   font-size: 1.1em;
}
.link a{
  text-decoration: none;
 
}

.messages {
   padding-left: 1.5em;
   padding: 2em;
}

.message {
   border: solid 1px ;
   margin-bottom: 2em;
   padding: 1em;
   border-radius: 1em 1em 1em 0;
   box-shadow: 10px 5px 5px rgba(128, 128, 128, 0.322);
}
.ava img {
   width: 80px;
}
.button {
   display: flex;
   justify-content: right;
}
